.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.contact-row {
  display: flex;
  flex-wrap: wrap;
  
}

.contact-info, .contact-map {
  flex: 1;
  padding: 20px;
}

.contact-text {
  animation: fade-right 1s ease-in-out;
}

.heading-section {
  font-size: 2rem;
  margin-bottom: 20px;
}

.contact-details ul, .contact-social ul {
  list-style-type: none;
  padding: 0;
}

.contact-details li, .contact-social li {
  margin-bottom: 10px;
  
}

.contact-details a, .contact-social a {
  color: #000000;
  text-decoration: none;
  font-size: 1.2rem;
  display: inline-flex;
}

.hours-heading {
  font-weight: bold;
  margin-bottom: 10px;
}

.hours-content {
  font-size: 1rem;
}

.contact-map {
  animation: fade-left 1s ease-in-out;
}

.svg{
  color: #000000;
}

@keyframes fade-right {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade-left {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
